@import 'tailwindcss';

@theme {
    --inset-shadow: -1px -1px 2px 2px grey;
}

html,
body {
    height: 100%;
}

body {
    margin: 0;
    background: url('assets/background.png');
    background-position: center;
    background-size: cover;
    font-family: 'Space Mono', 'Courier New', 'Monaco', 'Menlo', monospace;
}
